.PROJECTSCONTAINER {
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 35px;
}
.PROJECTSTITLE {
  background-image: linear-gradient(180deg, #ff8660ff 0%, #d5491dff 100%);
  background-clip: text;
  color: transparent;
  font-size: 35px;
  font-weight: 800;
  margin-top: 35px;

  text-transform: uppercase;
  white-space: nowrap;
  text-align: center;
}

/* ----------------------------- */

#projects {
  padding: 1rem;
  background-color: #1e1e1e;
  color: #fff;
}

.PROJECTSTITLE {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

.allProjects {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

.projectCard {
  background-color: #282828;
  border-radius: 8px;
  overflow: hidden;
  width: 300px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Add transition for hover effect */
}

.projectCard:hover {
  transform: translateY(-5px); /* Move the card up slightly on hover */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add a shadow for elevation effect */
}


.projectImage {
  width: 100%;
  height: auto;
}

.projectInfo {
  padding: 1rem;
}

.projectTitle {
  font-size: 1.5rem;
  margin: 0.5rem 0;
}

.projectDescription {
  font-size: 1rem;
  color: #aaa;
}

.projectLink {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  color: #fff;
    font-weight: bold;
  background-color: #ff6600;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.3s;
}

.projectLink:hover {
  background-color: #ff4500;
    text-decoration: underline;

}
